.title-withLine{
    font-size: 17px;
    font-weight: 600;
    border-bottom: 2px solid var(--Wedgewood);
    padding-bottom: 18px;
    position: relative;
    display: inline-block;
    margin: 0;
    &::after{
        position: absolute;
        content: "";
        height: 2px;
        width: 35px;
        left: 0;
        bottom: -2px;
        background-color: var(--Flamenco);
    }
}
.section-title{
    font-size: 20px;
    font-weight: 700;
    margin: 0 0 30px;
    text-transform: uppercase;
    color: var(--Wedgewood);
    text-align: center;
    span{
        position: relative;
        margin-right: 10px;
        &::after{
            position: absolute;
            content: "";
            width: 6px;
            height: 6px;
            border-radius: 50%;
            background-color: var(--Flamenco);
            top: 6px;
            right: -11px;
            display: block;
        }
    }
}
h1.section-title{
    text-align: left;
}
.subtitle{
    font-size: 14px;
    color: var(--White);
    font-weight: 400;
    line-height: 20px;
    margin: 7px 0 0;
}
@media(max-width: 767px){
    .section-title{
        margin-bottom: 30px;
    }
    .subtitle{
        font-size: 14px;
    }
}
@media(max-width: 575px){
    .section-title{
        font-size: 21px;
        margin-bottom: 20px;
    }
}